.header {
    width: 100%;
    height: 50px;
    background-color: var(--primary);
    color: white;

    display: flex; /* fica um do lado do outro */
    align-items: center; /* fica centralizado na vertical  */ 
    justify-content: space-around; /* espa√ßo a esquerda e a direita */
}

.header span {
    font-size: 1.5rem; /* 16px = 1 rem*/
    font-weight: bold; 
}

.header a {
    text-decoration: none;
    color: var(--write);
    padding-right: 1.5rem;
    font-size: 1.125rem; /* 18px */
}

.header a:hover {
    color: var(--red);
    
}